$rosa: #d94267;
$bege-claro: #fff0e2;
$bege: #ffd7a9;
$laranja: #f49e48;

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

body {
  background: $bege-claro;
}

.questions {
  background-color: $bege;
  max-width: 340px;
  width: 100%;
  margin: 0 auto;
  margin-top: 50px;


  &__btn {
    display: block;
    color: $laranja;
    box-sizing: border-box;
    padding: 10px 12px;
    position: relative;

    &::after {
      content: 'i';
      position: absolute;
      right: 20px;
    }

    &.ativo {
      &::after {
        transform: rotate(90deg);
      }
    }
  }
}